import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
import { Route, Redirect } from "react-router-dom";
import { TokenCheck } from "./redux/actions";
import LOADERS from "./component/Misc/Loaders";
import "../styles/styles.scss";
const LOGIN = lazy(() => import("./component/Login/Login"));
const SIGNUP = lazy(() => import("./component/Signup/Signup"));
const STEPS = lazy(() => import("./component/Signup/Steps/Container"));
const MEMBERPAYMENTFORM = lazy(() =>
  import("./component/Biling/MemberShipForm")
);
const PANELSETTING = lazy(() => import("./component/Setting/Panel"));
const HOME = lazy(() => import("./component/Home/Home"));
const TEST = lazy(() => import("./test"));
const MENU = lazy(() => import("./component/Navigation/SideMenu"));
const CHAT = lazy(() => import("./component/chat/InAppMessage/InApp"));
const SEARCH = lazy(() => import("./component/Search/Search"));
const HELPPAGE = lazy(() => import("./component/Help/HelpPage"));
const HELPINSIDE = lazy(() => import("./component/Help/HelpInside"));
const CALEND = lazy(() => import("./component/common/Date"));
const Forget = lazy(() => import("./component/forgetPassword/screen1"));
const Forget2 = lazy(() => import("./component/forgetPassword/screen2"));
const Forget3 = lazy(() => import("./component/forgetPassword/screen3"));
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount = () => {
    this.props.token_check();
  };
  render() {
    return (
      <React.Fragment>
        <Suspense fallback={<LOADERS />}>
          <MENU />
          <Route
            exact
            path="/"
            render={() =>
              this.props.Login.auth === true ? (
                <HOME />
              ) : (
                <Redirect to="/login" />
              )
            }
          />
          <Route exact path="/test" component={TEST} />
          <Route
            exact
            path="/login"
            render={() =>
              this.props.Login.auth !== false ? <LOGIN /> : <Redirect to="/" />
            }
          />
          <Route
            exact
            path="/signup"
            render={() =>
              this.props.Login.auth !== false ? (
                <SIGNUP />
              ) : (
                <Redirect to="/login" />
              )
            }
          />
          <Route
            exact
            path="/step"
            render={() =>
              this.props.Login.auth !== false ? (
                <STEPS />
              ) : (
                <Redirect to="/login" />
              )
            }
          />
          <Route
            exact
            path="/payment"
            render={() =>
              this.props.Login.auth !== false ? (
                <MEMBERPAYMENTFORM />
              ) : (
                <Redirect to="/login" />
              )
            }
          />
          <Route
            exact
            path="/profile-setting"
            render={() =>
              this.props.Login.auth !== false ? (
                <PANELSETTING />
              ) : (
                <Redirect to="/login" />
              )
            }
          />
          <Route
            exact
            path="/chat"
            render={() =>
              this.props.Login.auth !== false ? (
                <CHAT />
              ) : (
                <Redirect to="/login" />
              )
            }
          />
          <Route
            exact
            path="/search"
            render={() =>
              this.props.Login.auth !== false ? (
                <SEARCH />
              ) : (
                <Redirect to="/login" />
              )
            }
          />
          <Route
            exact
            path="/forget-password"
            render={() =>
              this.props.Login.auth !== false ? (
                <Forget />
              ) : (
                <Redirect to="/login" />
              )
            }
          />
          <Route
            exact
            path="/forget-email"
            render={() =>
              this.props.Login.auth !== false ? (
                <Forget2 />
              ) : (
                <Redirect to="/login" />
              )
            }
          />
          <Route
            exact
            path="/forget-mail"
            render={() =>
              this.props.Login.auth !== false ? (
                <Forget3 />
              ) : (
                <Redirect to="/login" />
              )
            }
          />
          <Route exact path="/help" component={HELPPAGE} />
          <Route exact path="/helpinside" component={HELPINSIDE} />
          <Route exact path="/date" component={CALEND} />
        </Suspense>
      </React.Fragment>
    );
  }
}
const mapStateToProps = state => {
  return {
    ...state
  };
};
const mapDispatchToProps = dispatch => {
  return {
    token_check: () => {
      dispatch(TokenCheck());
    }
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(App);
