import {createStore, applyMiddleware, compose, combineReducers} from "redux";
import thunk from "redux-thunk";
import logger from "redux-logger";
import {LoginReducer} from "../reducers/Login";
import {MenuReducer} from "../reducers/Menu";
import {SlideReducer} from "../reducers/Slide";
import {reviewSlide} from "../reducers/Slide";
import {SearchMovie} from "../reducers/Search";
const rootReducers = combineReducers({
    Login: LoginReducer,
    Menu:MenuReducer,
    Slide:SlideReducer,
    ReviewExpand:reviewSlide,
    SearchMovies:SearchMovie
})
export const store = createStore(rootReducers, compose(applyMiddleware(thunk,logger)));