import * as type from "../constant/Actions";
export const openMenu = payloads => {
    return dispatch => {
        dispatch({
            type: type.OPENMENU,
            payload:true
        })
    }
}
export const closeMenu = payloads => {
    return dispatch => {
        dispatch({
            type: type.CLOSEDMENU,
            payload:false
        })
    }
}