import {
    OPENMENU,
    CLOSEDMENU
} from "../constant/Actions";
export const MenuReducer = (state = {
    openMenu: false
}, action) => {
    switch (action.type) {
        case OPENMENU:
            return {
                ...state,
                openMenu: true,
            }
        case CLOSEDMENU:
            return {
                openMenu: false
            }
        default:
            return {
                ...state
            }
    }
}