import * as type from "../constant";
import axios from "axios";
export const ON_LOGIN = payload => {
    return dispatch => {
        axios.post("http://167.71.186.205/api/auth/sign_in", payload).then((res) => {
            localStorage.clear();
            let token = res.data.token;
            localStorage.setItem("TOKEN", token);
            dispatch({
                type: type.ONLOGIN,
                payload:res.data.user
            })
        }).catch(error => {
            console.log(error);
        })
    }
}