import axios from "axios";
import * as type from '../constant/Actions';
import * as Menu from "./Menu";
import * as Seaching from "./Search";
import * as SignUp from "./Signup";
import * as LogIn from "./Login";
export const Signup = SignUp;
export const Search = Seaching;
export const Menubar = Menu;
export const Login = LogIn;
export const slideChange = e => {
    return dispatch => {
        dispatch({
            type: type.SLIDECHANGE,
            payload: e
        })
    }
}
export const reviewHandle = e => {
    return dispatch => {
        dispatch({
            type: type.REVIEWEXPAND,
            payload: e
        })
    }
}
export const TokenCheck = (e) => {
    return dispatch => {
        let TOKEN = localStorage.getItem("TOKEN");
        if (TOKEN) {
            axios.get("http://167.71.186.205/api/me", {
                headers: {
                    Authorization: TOKEN
                }
            }).then(res => {
                dispatch({
                    type: type.ONLOGIN,
                    payload: res.data.user
                })
            }).catch(error => {
                console.log(error)
            })
        }
    }
}
export const LogOut = e => {
    return dispatch => {
        localStorage.clear();
        dispatch({
            type: type.ONLOGOUT
        })
    }
}