import * as type from "../constant/Actions";
const initState = {
    auth: true,
    name: "",
    username: "",
    email: "",
    error: {
        message: "",
        code: ""
    },
    dob: ""
}
export const LoginReducer = (state = initState, action) => {
    switch (action.type) {
        case type.ONLOGIN:
            const { username, email } = action.payload;
            const { name, dob } = action.payload.contactData;
            return {
                ...state,
                auth: true,
                name,
                username,
                email,
                dob

            }
        case type.ONLOGOUT:
            return {
                ...initState
            }
        default:
            return {
                ...state
            }
    }
}