import * as type from "../constant/Actions";
export const SIGN_UP = (payload) => {
    return dispatch => {
        dispatch({
            type: type.ONSIGNUP,
            payload
        })
    }
}
export const REGISTERED = payload =>{
    return dispatch=>{
        dispatch({
            type:type.ONREGIGISTERED,
            payload:payload
        })
    }
}